  .recreation__list {
    margin-bottom: 18px;
  }


  .recreation__housing-text {

    margin-top   : 22px;
    padding-left : 48px;
    padding-right: 49px;

  }

  .recreation__souvenir-text {
    padding-left : 114px;
    padding-right: 94px
  }

  .recreation__food-text {
    margin-top   : 22px;
    padding-left : 50px;
    padding-right: 49px;

  }

  .recreation__housing {

    padding-top : 141px;
