.criterion {
  width           : 320px;
  height          : 470px;
  margin          : 0 auto;
  background-color: @color-blue-background;
  color           : @color-white-color ;
}

.criterion__container {
  position      : relative;
  margin-top    : 26px;
  padding-top   : 56px;
  padding-right : 27px;
  padding-bottom: 41px;
  padding-left  : 28px;
}

.criterion__container::before {
  content            : "";
  position           : absolute;
  top                : -21px;
  left               : 0;
  width              : 320px;
  height             : 59px;
  background-image   : url(../img/bg-triangle.svg);
  background-repeat  : no-repeat;
  background-position: 0 0;
}

.criterion__city {
  margin     : 0;
  font-size  : 18px;
  line-height: 26px;
  font-weight: bold;
  text-align : center;
}

.criterion__number {
  margin     : 0;
  margin-top : 9px;
  font-size  : 14px;
  line-height: 21px;
  text-align : center;
}

.criterion__text {
  margin     : 0;
  margin-top : 16px;
  font-size  : 14px;
  line-height: 21px;
  text-align : center;
}

@media (min-width: @tablet-width) {
  .criterion {
    width : @tablet-width;
    height: auto;
  }

  .criterion__wrapper {
    display: flex;
    height : 256px;
  }

  .criterion__container {

    padding-top   : 19px;
    padding-right : 40px;
    padding-bottom: 49px;
    padding-left  : 42px;
  }

  .criterion__container::before {
    content: "";
    display: none;
  }

  .criterion__city {
    margin     : 0;
    font-size  : 21px;
    line-height: 26px;
    font-weight: bold;
    text-align : center;
  }

  .criterion__number {
    margin     : 0;
    margin-top : 9px;
    font-size  : 14px;
    line-height: 21px;
    text-align : center;
  }

  .criterion__text {
    margin       : 0;
    margin-top   : 16px;
    padding-top  : 8px;
    padding-left : 4px;
    padding-right: 4px;
    font-size    : 14px;
    line-height  : 21px;
    text-align   : center;
  }

  .criterion__image {
    width : 512px;
    height: 256px;
  }
}

@media(min-width:@desktop-width) {
  .criterion {
    width: @desktop-width;
  }

  .criterion__image {
    width: 800px;
  }

  .criterion__container {
    width         : 400px;
    padding-top   : 25px;
    padding-right : 63px;
    padding-bottom: 63px;
    padding-left  : 67px
  }

  .criterion__city {
    padding-left : 27px;
    padding-right: 30px;
  }



  .criterion__number {
    margin       : 0;
    padding-top  : 11px;
    padding-right: 2px;

  }

  .criterion__text {
    margin     : 0;
    padding-top: 22px;
  }
}
